import * as React from 'react';
import Form, { useForm } from '@miq/formjs';

import { Button, ConfirmButton, Icons } from '@miq/componentjs';

export const AttributeUpdateForm = ({ data, productInstance, ...props }) => {
  const form = useForm({ name: data.name || '', value: data.value || '' });

  if (!data?.slug || !productInstance?.slug) return null;
  const { onSuccess, onError } = props;

  return (
    <Form
      context={form}
      onSubmit={(e) => {
        e.preventDefault();

        productInstance
          .patchAttribute(data.slug, form.values)
          .then((res) => onSuccess?.(res))
          .catch((err) => {
            form.handleError(err);
            return onError?.(err);
          });
      }}
    >
      <div className="d-grid grid-md-6 gap-1">
        <div className="span-md-2">
          <AttrNameField error={form.errors.name} />
        </div>

        <div className="span-md-3">
          <AttrValueField error={form.errors.value} className="" />
        </div>

        <div className="span-md-1">
          <Form.Submit value="Save" className="btn-primary-2 me-2" />
          <AttributeDeleteButton {...{ productInstance, data, onSuccess, onError }} />
        </div>
      </div>
    </Form>
  );
};

const formDefaultValues = { name: '', value: '' };
export const AttributeCreateForm = ({ productInstance, ...props }) => {
  const form = useForm(formDefaultValues);

  if (!productInstance?.slug) return null;

  return (
    <Form
      context={form}
      onSubmit={(e) => {
        e.preventDefault();

        productInstance
          .postAttribute(form.values)
          .then((res) => {
            form.setValues(formDefaultValues);
            return props?.onSuccess?.(res);
          })
          .catch((err) => {
            form.handleError(err);
            return props?.onError?.(err);
          });
      }}
    >
      <div className="d-grid grid-md-6 gap-1">
        <div className="span-md-2">
          <AttrNameField error={form.errors.name} />
        </div>

        <div className="span-md-3">
          <AttrValueField error={form.errors.value} className="" />
        </div>

        <div className="span-md-1">
          <Form.Submit value="Save" className="btn-primary-2" />
        </div>
      </div>
    </Form>
  );
};

const AttrNameField = (props) => {
  const { label, text, error, placeholder = `Entrez le nom de l'attribut ...`, ...rest } = props;

  return (
    <Form.Field {...{ label, text, error }}>
      <Form.Text {...rest} required name="name" placeholder={placeholder} maxLength={30} minLength={2} />
    </Form.Field>
  );
};
const AttrValueField = (props) => {
  const { label, text, error, placeholder = `Entrez la valuer de l'attribut ...`, ...rest } = props;

  return (
    <Form.Field {...{ label, text, error }}>
      <Form.Text {...rest} required name="value" placeholder={placeholder} maxLength={99} minLength={2} />
    </Form.Field>
  );
};

const AttributeDeleteButton = ({ data, productInstance, ...props }) => {
  if (!data?.slug || !productInstance?.slug) return null;
  const { onSuccess, onError, ...rest } = props;

  return (
    <ConfirmButton
      {...rest}
      renderHeader={() => <div>Supprimer un attribut</div>}
      renderFooter={({ setOpen }) => (
        <div className="d-flex justify-content-between align-items-center">
          <Button onClick={() => setOpen(false)}>Annuler</Button>

          <Button
            onClick={() =>
              productInstance
                .deleteAttribute(data.slug)
                .then((res) => {
                  setOpen(false);
                  return onSuccess?.(res);
                })
                .catch((err) => onError?.(err))
            }
            className="btn-danger"
          >
            Supprimer
          </Button>
        </div>
      )}
      render={() => (
        <div className="p-3">
          Are you sure you want to delete this attribute:
          <div className="fw-bold">{data.name}</div>
        </div>
      )}
      className="attr-delete-button btn-danger-3"
    >
      <Icons.Trash />
    </ConfirmButton>
  );
};
