import * as React from 'react';

import lang from './views.lang';
import formLang from './forms/lang';

import Staff, {
  StaffImg,
  StaffImgAddButton,
  StaffImgDeleteButton,
  StaffImgUpdateButton,
  StaffImgViewSelect,
} from '@miq/staffjs';
import { TLang } from '@miq/utiljs';
import { Button, Icons, TImg } from '@miq/componentjs';
import Form, { MetaDescriptionField, MetaSlugField, MetaTitleField, useForm } from '@miq/formjs';

import { CategoryChoicesType } from '../types';

import { StaffProduct } from './models';
import { SizeAddForm, SizeUpdateForm } from './Size';
import { ProductForm, ProductUpdateForm } from './forms';
import { AttributeCreateForm, AttributeUpdateForm } from './Attribute';
import { ProductDeleteButton, ProductImagesAddButton } from './buttons';

const i18n = (k: keyof TLang) => window.i18n(lang, k);

export type ProductUpdateTabProps = {
  form: ReturnType<typeof useForm>;
  instance: ReturnType<typeof StaffProduct>;
  onUpdate: (data: any) => void;
  categories: CategoryChoicesType;
};

export const ProductUpdateInfoTab = (props: ProductUpdateTabProps) => {
  const { form, instance, categories, onUpdate } = props;
  const { errors } = form;
  const PFormFields = (
    <>
      <ProductForm.NameField
        error={errors.name}
        label={window.i18n(formLang, 'NField.label')}
        text={window.i18n(formLang, 'NField.text')}
      />

      <div className="d-grid grid-md-2 gap-1">
        <ProductForm.CategoryField
          error={errors.category}
          categories={categories}
          label={window.i18n(formLang, 'CatField.label')}
        />
        <ProductForm.RetailPriceField label={window.i18n(formLang, 'RetailPField.label')} error={errors.retail_price} />
      </div>

      <ProductForm.PresaleCheckboxField
        error={errors.is_pre_sale}
        label={window.i18n(formLang, 'PresaleField.label')}
      />
      {form.values.is_pre_sale && (
        <ProductForm.PresaleTextField
          text={window.i18n(formLang, 'PresaleTextField.text')}
          error={errors.is_pre_sale_text}
        />
      )}

      <ProductForm.OnSaleCheckboxField error={errors.is_on_sale} label={window.i18n(formLang, 'OnsaleField.label')} />
      {form.values.is_on_sale && (
        <ProductForm.SalePriceField label={window.i18n(formLang, 'SalePField.label')} error={errors.sale_price} />
      )}

      <ProductForm.DescriptionField
        error={errors.description}
        label={window.i18n(formLang, 'DField.label')}
        text={window.i18n(formLang, 'DField.text')}
      />

      <div className="my-4">
        <ProductForm.OosCheckboxField error={errors.is_oos} />
      </div>

      <div className="my-4">
        <ProductForm.IsExplicitCheckboxField error={errors.is_explicit} />
      </div>
    </>
  );

  return (
    <Staff.Section>
      <ProductUpdateForm
        context={form}
        {...{
          instance,
          fields: [
            'name',
            'category',
            'description',
            'retail_price',
            'is_pre_sale',
            'is_pre_sale_text',
            'is_pre_sale_dt',
            'is_on_sale',
            'sale_price',
            'is_oos',
            'is_explicit',
          ],
          onSuccess: ({ data }) => onUpdate(data),
        }}
      >
        <Staff.Section
          // border
          title={i18n('InfoTab.title')}
          text={i18n('InfoTab.text')}
          footer={
            <div className="d-flex justify-content-center">
              <Form.Submit value={i18n('Btn.saveLabel')} className="btn-primary min-w-25 fw-bold" />
            </div>
          }
        >
          {PFormFields}
        </Staff.Section>
      </ProductUpdateForm>

      <Staff.Section border title={i18n('SeoTab.title')}>
        <ProductUpdateForm
          context={form}
          {...{
            instance,
            fields: ['meta_title', 'meta_description', 'meta_slug'],
            onSuccess: ({ data }) => onUpdate(data),
          }}
        >
          <MetaTitleField error={errors.meta_title} />
          <MetaSlugField error={errors.meta_slug} />
          <MetaDescriptionField error={errors.meta_description} />

          <div className="d-flex justify-content-center">
            <Form.Submit value={i18n('Btn.saveLabel')} className="btn-primary-3 min-w-25 fw-bold" />
          </div>
        </ProductUpdateForm>
      </Staff.Section>
    </Staff.Section>
  );
};

export const ProductUpdateInventoryTab = (props: any) => {
  const { instance, onUpdate } = props;
  const [addAtr, setAddAtr] = React.useState(false);
  const [addSize, setAddSize] = React.useState(false);

  return (
    <Staff.Section title={i18n('InventoryTab.title')}>
      <div className="d-grid grid-md-3 gap-1">
        <div className="span-md-2">
          <Staff.Section
            title="Tailles"
            text="Ajouter des tailles"
            actions={
              <Button onClick={() => setAddSize(!addSize)} className="btn-primary-3">
                Ajouter une taille
              </Button>
            }
          >
            {addSize && (
              <Staff.Section text="Ajouter une taille">
                <SizeAddForm productInstance={instance} onSuccess={({ data }: any) => onUpdate(data)} />
              </Staff.Section>
            )}

            {instance.sizes.map((size: any) => {
              return (
                <SizeUpdateForm
                  data={size}
                  productInstance={instance}
                  onSuccess={({ data }: any) => onUpdate(data)}
                  key={size.slug}
                />
              );
            })}
          </Staff.Section>

          <Staff.Section
            title="Attributs"
            actions={
              <Button onClick={() => setAddAtr(!addAtr)} className="btn-primary-3">
                Ajouter un attribut
              </Button>
            }
          >
            {addAtr && (
              <Staff.Section text="Ajouter un attribut au produit.">
                <AttributeCreateForm productInstance={instance} onSuccess={({ data }: any) => onUpdate(data)} />
              </Staff.Section>
            )}

            {instance.attributes.map((attr: any) => (
              <div className="mb-1" key={attr.slug}>
                <AttributeUpdateForm
                  data={attr}
                  productInstance={instance}
                  onSuccess={({ data }: any) => onUpdate(data)}
                />
              </div>
            ))}
          </Staff.Section>
        </div>
      </div>
    </Staff.Section>
  );
};

export const ProductUpdateSettingsTab = (props: any) => {
  const { instance, listRes, navigate } = props;

  return (
    <Staff.Section title={i18n('SettingsTab.title')}>
      <Staff.Section
        title={i18n('SettingsTab.deleteSectionTitle')}
        actions={
          <ProductDeleteButton
            {...{ instance }}
            onSuccess={() => {
              listRes.refresh();
              return navigate(props.back);
            }}
            onError={(res) => {
              if (res.status === 404) navigate('../');
            }}
          />
        }
      />
    </Staff.Section>
  );
};

export const ProductUpdateMediaTab = (props: any) => {
  const { instance, onUpdate } = props;
  const { cover_data } = instance;
  return (
    <Staff.Section
      title={i18n('MediaTab.title')}
      actions={<ProductImagesAddButton productInstance={instance} onSuccess={(res: any) => onUpdate?.(res.data)} />}
    >
      <div className="d-grid grid-md-3 grid-lg-4 gap-2">
        <CoverCard
          {...props}
          img={StaffImg(cover_data)}
          onDeleteSuccess={() => onUpdate({ cover_data: null, cover: null })}
        />

        {instance.images_data.map((image: TImg) => {
          const img = StaffImg(image);
          const swapCover = () => instance.swapCover(img.slug).then(({ data }: any) => onUpdate(data));
          return (
            <ImageCard
              {...props}
              img={img}
              actions={<Staff.Button onClick={swapCover}>Set as cover</Staff.Button>}
              onDeleteSuccess={() =>
                onUpdate({
                  images_data: instance.images_data.filter((i: any) => i.slug !== img.slug),
                  images: instance.images.filter((i: any) => i !== img.slug),
                })
              }
              key={img.slug}
            />
          );
        })}
      </div>
    </Staff.Section>
  );
};

export const getImageComponent = (view: string) => {
  switch (view) {
    case 'mobile':
      return StaffImg.Mobile;
    case 'square':
      return StaffImg.Square;
    case 'thumb':
      return StaffImg.Thumb;
    case 'picture':
      return StaffImg.Picture;
    default:
      return StaffImg.Image;
  }
};

const CoverCard = (props: any) => {
  const { img, onUpdate, instance } = props;
  const form = useForm({ view: 'src' });

  if (!img.slug)
    return (
      <div className="p-cover-add-btn d-flex align-items-center justify-content-center flex-column text-center border-1 rounded p-3">
        <p className="text-sm text-muted mb-3">Ce produit n'a pas d'image principale</p>
        <StaffImgAddButton
          onSuccess={({ data }: any) => {
            instance
              .update({ cover: data.slug })
              .then(({ data }: any) => onUpdate(data))
              .catch((err: any) => {});
          }}
          label="Ajouter une image"
          className="btn-primary-3 btn-md"
        />
      </div>
    );

  const Image = getImageComponent(form.values.view);
  return (
    <Form context={form} className="p-cover-update-form">
      <Staff.Section
        border
        title={<ImgDeleteButton {...props} img={img} />}
        actions={<StaffImgViewSelect required name="view" />}
        className="p-image-card"
      >
        <StaffImgUpdateButton
          instance={img}
          field={form.values.view}
          onSuccess={({ data }: any) => onUpdate({ cover: data.slug, cover_data: data })}
        >
          <Image {...img} />
        </StaffImgUpdateButton>
      </Staff.Section>
    </Form>
  );
};

const ImageCard = ({ img, actions, ...props }: any) => {
  return (
    <Staff.Section border title={<ImgDeleteButton {...props} img={img} />} actions={actions} className="p-image-card">
      <StaffImg.Image {...img} />
    </Staff.Section>
  );
};

const ImgDeleteButton = ({ img, onDeleteSuccess }: any) =>
  img && img.slug ? (
    <StaffImgDeleteButton onSuccess={onDeleteSuccess} instance={img} className="btn-danger-3">
      <Icons.Trash />
    </StaffImgDeleteButton>
  ) : null;
