import * as React from 'react';
import { useParams, useNavigate, useSearchParams } from 'react-router-dom';

import './views.update.scss';
import lang from './views.lang';

import Staff from '@miq/staffjs';
import { useForm } from '@miq/formjs';
import { useRequest } from '@miq/hookjs';
import { Loading, QueryButton } from '@miq/componentjs';
import { addForwardSlash, Response, TLang } from '@miq/utiljs';

import { emitter } from '../events';
import { StaffProduct } from './models';
import { PublishButton, UnPublishButton } from '../components';
import {
  ProductUpdateInfoTab,
  ProductUpdateInventoryTab,
  ProductUpdateMediaTab,
  ProductUpdateSettingsTab,
} from './views.UpdateTabs';
import { ProductCardItem } from './components';

const i18n = (k: keyof TLang) => window.i18n(lang, k);

//#region FIELDS

const formInfoFields = [
  'name',
  'category',
  'description',
  'retail_price',
  'is_pre_sale',
  'is_pre_sale_text',
  'is_pre_sale_dt',
  'is_oos',
  'is_on_sale',
  'sale_price',
  'is_explicit',
];
const configFormFields = ['cost', 'color_group_pk', 'stage'];
const supplierFormFields = [
  'supplier_name',
  'supplier_item_id',
  'supplier_item_sn',
  'supplier_item_category',
  'supplier_item_url',
  'supplier_item_cost',
  'supplier_item_cost_currency',
  'supplier_order_id',
];
const seoFields = ['meta_title', 'meta_description', 'meta_slug'];
const formFields = [...formInfoFields, ...configFormFields, ...supplierFormFields, ...seoFields];
const formDefaultValues: any = {
  retail_price: 0.0,
  is_pre_sale: false,
  sale_price: 0.0,
  is_on_sale: false,
  is_published: false,
};
const mapData2Values = (data: any = {}) => {
  const values: any = {};
  formFields.forEach((f) => {
    const value = data?.[f];
    if (value == null) {
      values[f] = formDefaultValues[f] ?? '';
      return;
    }
    values[f] = value;
  });
  return { ...formDefaultValues, ...values };
};

//#endregion FIELDS

//#region ProductStaffUpdateView

export default function ProductStaffUpdateView(props: any) {
  let { back } = props;
  const { prodSlug } = useParams();
  const [params] = useSearchParams();
  const navigate = useNavigate();

  const form = useForm(mapData2Values());
  const { res, setRes, loading, ...request } = useRequest(() => StaffProduct({ slug: prodSlug }).retrieve(), {
    refreshDeps: [prodSlug],
    onSuccess: ({ data }) => form.setValues(mapData2Values(data)),
    onError: ({ response }) => {
      if (response?.status === 404) return navigate('../');
    },
  });

  const r = new Response(res);
  if (!r.isSuccess) return null;

  if (loading) return <Loading />;

  const instance = StaffProduct(r.data);

  const updateData = (d: any) => {
    request.updateData(d);
    emitter.update(d);
  };

  const tab = params.get('tab');

  const tabsProps = {
    instance,
    form,
    setRes,
    onUpdate: updateData,
    navigate,
    back,
    params,
    tab,
    categories: r?.data?.categories,
  };

  return (
    <Staff.View
      className="product-staff-update-view"
      requiredPerms={['store.change_product']}
      title={<ProductCardItem data={{ ...instance.export() }} />}
      back={`${addForwardSlash(back)}${`${params}` ? `?${params}` : ''}`}
    >
      <Staff.Section
        title={<div className="mb-1">{QButtons}</div>}
        actions={
          instance.is_published ? (
            <UnPublishButton instance={instance} onSuccess={({ data }) => updateData(data)}>
              DÃ©publier
            </UnPublishButton>
          ) : (
            <PublishButton
              instance={instance}
              onSuccess={({ data }) => updateData(data)}
              onError={(err) => form.handleError(err)}
            >
              Publier
            </PublishButton>
          )
        }
      >
        {(() => {
          switch (tab) {
            case 'inventory':
              return <ProductUpdateInventoryTab {...tabsProps} />;
            case 'settings':
              return <ProductUpdateSettingsTab {...tabsProps} />;
            case 'media':
              return <ProductUpdateMediaTab {...tabsProps} />;

            default:
              return <ProductUpdateInfoTab {...tabsProps} />;
          }
        })()}
      </Staff.Section>
    </Staff.View>
  );
}

//#endregion ProductStaffUpdateView

const QButtons = (
  <div className="query-buttons">
    <QueryButton index target="tab" className="btn-md">
      Info
    </QueryButton>
    <QueryButton target="tab" value="media" className="btn-md">
      {i18n('MediaTab.title')}
    </QueryButton>
    <QueryButton target="tab" value="inventory" className="btn-md">
      {i18n('InventoryTab.title')}
    </QueryButton>
    <QueryButton target="tab" value="settings" className="btn-md">
      {i18n('SettingsTab.title')}
    </QueryButton>
  </div>
);
